// адаптивный видео-блок
.video-container {
  position: relative;
  padding-bottom: 56.25%;
  padding-top: 30px;
  height: 0;
  overflow: hidden;
  border: 1px solid;

  & iframe,
  & object,
  & embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
}

// Полпробела
span.tsp {
  display: inline-block;
  width: .12em;
  overflow: hidden;
  text-decoration: inherit;
}

hr {
  margin-top:    $line-height-computed;
  margin-bottom: $line-height-computed;
  border: 0;
  border-top: 1px solid $hr-border;
}

h1, h2, h3, h4, h5, h6,
.h1, .h2, .h3, .h4, .h5, .h6 {
  font-family: $headings-font-family;
  font-weight: $headings-font-weight;
  line-height: $headings-line-height;
  color: $headings-color;

  small,
  .small {
    font-weight: normal;
    line-height: 1;
    color: #777;
  }
}

h1, .h1,
h2, .h2,
h3, .h3 {
  margin-top: $line-height-computed;
  margin-bottom: ($line-height-computed / 2);

  small,
  .small {
    font-size: 65%;
  }
}

h4, .h4,
h5, .h5,
h6, .h6 {
  margin-top: ($line-height-computed / 2);
  margin-bottom: ($line-height-computed / 2);

  small,
  .small {
    font-size: 75%;
  }
}

h1, .h1 { font-size: $font-size-h1; }

h2, .h2 {
  font-size: $font-size-h2;
  color: #1c1024;
  font-family: 'FuturaDemiC';
  font-weight: 400;
  line-height: 30px;
  margin: 0;
  padding: 0;
}

h3, .h3 { font-size: $font-size-h3; }
h4, .h4 { font-size: $font-size-h4; }
h5, .h5 { font-size: $font-size-h5; }
h6, .h6 { font-size: $font-size-h6; }

p {
  color: #7a7f8b;
  font-family: 'FuturaDemiC';
  font-size: 17px;
  font-weight: 400;
  line-height: 24px;

  &:last-child { margin-bottom: 0; }
}

// Отступы
p, ul, ol, pre, table, blockquote {
  margin-top: 0px;
  margin-bottom: 21px;
}

ul ul, ol ol, ul ol, ol ul {
  margin-top: 0px;
  margin-bottom: 0px;
}

code {
  padding: 2px 4px;
  font-size: 90%;
  color: $code-color;
  background-color: $code-bg;
  border-radius: $border-radius-base;
}

.box-headline {
  text-align: center;

  &.text-left {
    text-align: left;
  }
}

.box-actions {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;

  &.right {
    justify-content: flex-end;
  }
}

mark {
  background-color: #fedf11;
  color: inherit;
  padding: 1px 2px;
}

.text-left { text-align: left; }
.text-right { text-align: right; }
.text-center { text-align: center; }
.float-left { float: left; }
.float-right { float: right; }

.text {
  padding-bottom: 30px;

  h3, .h3 {
    color: #1c1024;
    font-family: 'FuturaDemiC';
    font-size: 24px;
    font-weight: 600;
    font-style: italic;
    line-height: 24px;
  }

  p {
    font-size: 17px;
    font-weight: 600;
    color: #7a7f8b;
  }

  .box-actions {
    padding-top: 15px;
  }

  .img-in-text {
    padding-right: 30px;
  }
}
